.statElement {
    height: 220px;
    // width: 100%;
    max-width: 220px;
    // max-height: 256px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 256px;
    padding: 15px;
    // padding: 15px 15px 22px 15px;
    box-sizing: border-box;
    align-items: center;
    border-radius: 25px;
    background: rgba(255, 255, 255, 0.60);
    box-shadow: 0px 0px 23px 0px rgba(0, 0, 0, 0.35);
}

.statElement__title {
    margin:0;
    color: var(--Header, #07F);
    text-align: center;
    font-variant-numeric: lining-nums proportional-nums;
    font-family: Proxima Nova;
    font-size: 26px;
    // font-size: 70px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    // word-spacing: -8px;
}

.statElement__subtitle {
    margin: 0;
    color: var(--Header, #07F);
    text-align: center;
    font-variant-numeric: lining-nums proportional-nums;
    font-family: Proxima Nova;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.statElement__span {
    color: var(--Header, #07F);
    font-size: 43px;
    margin: 0;
}

@media screen and (max-width: 1024px) {

    .statElement{
        height: 160px;
        width: 160px;
    }

    .statElement__title {
        font-size: 20px;
    }
}