@import '../../variables/variaables.scss';

#gorbunov__photo {
    z-index: 0;
    position: relative;
    top: 1px;
    left: 40px;
}

#otinov__photo {
    z-index: 1;
}

.main {
    background-color: #F3F3F3;
    background-image: url("../../images/main-bg.jpg");
    background-repeat: no-repeat;
    background-position: right;
    background-size: contain;
    padding: 106px 0 0 0;
    overflow: hidden;
}

.main__wrap {
    max-width: 1440px;
    display: grid;
    grid-template-columns: minmax(400px, 637px) auto;
    overflow: hidden;
    padding: 0 0 0 140px;
}

.main__textContainer {
    display: flex;
    flex-direction: column;
    margin-bottom: 89px;
}

.main__imagesContainer {
    display: grid;
    grid-template-columns: 1fr minmax(160px, 800px);
    align-self: flex-end;
    justify-content: start;
    right: 70px;
    position: relative;
}

.main__image {
    width: auto;
    height: 100%;
}

.main__logo {
    color: #000;
    max-width: 700px;
    margin: 0 0 32px 0;
    font-variant-numeric: lining-nums proportional-nums;
    font-family: Proxima Nova;
    font-size: 54px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    text-align: left;
}

.main__logo::after {
    content: "«Просто»";
    color: #07F;
}

