.popup {
  width: 100vw;
  padding: 80px 20px;
  box-sizing: border-box;
  position: fixed;
  top: 0;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.8);
  display: none;
  align-items: center;
  overflow: hidden;
  height: 100%;
  z-index: 3;
  opacity: 0;
  transition: height 0ms 400ms, opacity 400ms 0ms;
}

.popup_visible {
  display: flex;
  opacity: 1;
  transition: 0.5 ease-out;
}

.popup__container {
  max-width: 857px;
  height: 100%;
  display: flex;
  flex-direction: row;
  gap: 25px;
  align-items: start;
  position: relative;
}

.popup_closeButton {
  width: 44px;
  height: 44px;
  align-self: end;
  margin: 0;
  padding: 0;
  background: none;
  border: none;
  position: absolute;
  top: -45px;
  right: -45px;
  transition: 0.25;
}
.popup_closeButton:hover {
  opacity: 0.8;
}

.popup_closeButtonImg {
  width: inherit;
  height: inherit;
  cursor: pointer;
  color: #FFF;
}

.popup__figure {
  width: inherit;
  height: 95%;
  margin: 0 auto;
}

.popup__image {
  height: 100%;
  border-radius: 10px;
}

.popup__caption {
  display: flex;
  justify-content: center;
  text-align: center;
  color: #FFF;
  font-variant-numeric: lining-nums proportional-nums;
  font-family: Proxima Nova;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 21px;
}

.popup__captionButton {
  width: 20px;
  height: 20px;
  padding: 0;
  margin: 0 10px;
  background: none;
  border: none;
  cursor: pointer;
}

.popup__captionButtonArrow {
  width: 100%;
  height: 100%;
  color: #FFF;
}

.popup__descriptionBar {
  width: 100%;
  min-width: 350px;
  padding-bottom: 20px;
}

.popup__descriptionBarTitle {
  margin: 0 0 24px 0;
  color: #FFF;
  font-variant-numeric: lining-nums proportional-nums;
  font-family: Proxima Nova;
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 21px;
}

.popup__descriptionBarSubTitle {
  margin: 0 0 24px 0;
  color: #FFF;
  font-variant-numeric: lining-nums proportional-nums;
  font-family: Proxima Nova;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 21px;
}

.popup__descriptionBarLink {
  text-decoration: none;
  color: #0077FF;
}

.popup__descriptionBarHighlight {
  font-weight: 700;
}

@media screen and (max-width: 984px) {
  .popup {
    padding: 100px 20px;
    align-items: flex-start;
  }
  .popup__container {
    flex-direction: column;
  }
  .popup_closeButton {
    right: -6px;
  }
  .popup__figure {
    margin-bottom: 20px;
  }
}/*# sourceMappingURL=Popup.css.map */