.stepCard {
  width: 188px;
  height: 166px;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 20px;
  position: relative;
  color: #D2D1D1;
  background: #F3F3F3;
}

.stepCard_active {
  background: #3390EC !important;
  color: #FFF !important;
}

.stepCard__number {
  width: 188px;
  margin: 0 0 5px 0;
  text-align: center;
  font-variant-numeric: lining-nums proportional-nums;
  font-family: "Proxima Nova";
  font-size: 60px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.stepCard__title {
  width: 170px;
  margin: 0;
  text-align: center;
  font-variant-numeric: lining-nums proportional-nums;
  font-family: "Proxima Nova";
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 16px;
}

.stepCard__checkmark {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  position: absolute;
  top: -30px;
  right: 0;
  color: #FFF;
  background-color: #28B289;
  transition: translateY(50px) 4s ease-in-out;
}

.stepCard_checked {
  color: #000;
}/*# sourceMappingURL=StepCard.css.map */