@import '../../variables/variaables.scss';

.methodFocus {
    width: 100%;
    display: grid; 
    grid-template-rows: 56px minmax(80px, auto);
    grid-template-columns: 55px auto;
    grid-column-gap: 14px;
    margin: 0 0 24px 0;
}

.methodFocus__number {
    grid-row: 1/3;
    margin: 0;

    color: #ECECEC;
    font-variant-numeric: lining-nums proportional-nums;
    font-family: "Proxima Nova";
    font-size: 100px;
    font-style: normal;
    font-weight: 300;
    line-height: normal;
}

.methodFocus__title {
    margin: 8px 0 0 0;
    align-self: center;

    color: $text-highlight-color;
    font-variant-numeric: lining-nums proportional-nums;
    font-family: "Proxima Nova";
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.methodFocus__subtitle {
    margin: 0;

    color: var(--Text-titleText, #454545);
    font-variant-numeric: lining-nums proportional-nums;
    font-family: "Proxima Nova";
    font-size: $paragraph-font-size;
    font-style: normal;
    font-weight: 400;
    line-height: 21px;
}

.methodFocus__subtitle_bold{
    font-weight: 700;
}

.methodFocus__link{
    color: $text-highlight-color !important;

}

@media screen and (max-width: 986px){
    .methodFocus{
        grid-template-rows: 56px minmax(160px, auto);
    }
}

@media screen and (max-width: 568px){
    .methodFocus{
        margin: 10px 0 20px 0;
        grid-template-rows: 80px minmax(180px, auto);

    }

    .methodFocus__number {
        grid-row: 1/2;
        margin: 0;
        align-self: center;
        justify-self: center;   

        font-size: 50px;
    }

    .methodFocus__subtitle {
        grid-column: 1/3;
        margin: 0 0 0 10px;
    }

    .methodFocus__list{
        padding-left: 15px;
    }
}