.docCard {
  height: 480px;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.docCard .docCard__image {
  width: 292px;
  height: 412px;
  border-radius: 20px;
  box-shadow: 0px 0px 23px 0px rgba(0, 0, 0, 0.35);
}
.docCard .docCard__caption {
  margin-top: 18px;
  color: #171A28;
  text-align: center;
  font-variant-numeric: lining-nums proportional-nums;
  font-family: Proxima Nova;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 21px;
}
.docCard .docCard__imageButton {
  border: none;
  background: none;
  width: auto;
  height: auto;
  cursor: pointer;
}
.docCard .docCard__imageButton:active {
  transform: translateX(1px) translateY(1px);
}

.docCard_invisible {
  display: none;
}

@media screen and (max-width: 548px) {
  .docCard {
    height: 520px;
  }
}/*# sourceMappingURL=DocCard.css.map */